.App {
  text-align: center;
  box-sizing: border-box;
  width: 40%;
  height: 100vh;
  background-color: gray;
  margin:  auto;
  position: relative;

    
}
.items{
  box-sizing: border-box;
  width: 55%;
  height: 50vh;
}
.itemOne{
  background-color: blue;
  opacity: 0.1;

}
.itemTwo{
  background-color: red;
  opacity: 0.1;

}
.itemThree{
  background-color: yellow;
  opacity: 0.1;

}
.itemFour{
  background-color: green;
  opacity: 0.1;

}

.firstRow , .secondRow{
  display: flex;
}

.down{
  opacity: 1;
  border: 10px solid white;
}
 
.score{
  z-index: 1;
  position: absolute;
bottom: 0;
text-align: center;
width: 100%;
font-size: 30px;
}
.start{
  width: 150px;
  height: 150px;
  border-radius: 50%;
  padding: 5px;
  margin:auto;
  cursor: pointer;
  background-color: green;
  border: none;
  position: absolute;
  z-index: 1;
  /* font-size: x-large; */
  /* font-weight: bold; */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  }
  .start:hover{
    background-color: rgb(56, 228, 56);
    color: white;
  }
  .start:active{
    border: 5px solid white;
  }
  .gameover{
    box-sizing: border-box;
    margin: 20vh 0;
    padding: 20px;
    background-color: red;
    /* height: 50vh; */
    width: 100%;
    font-size: 30px;
position: absolute;
z-index: 1;
  }
  .gameover button{
    background-color: royalblue;
    border-radius: 10px;
    height: 30px;
    margin: 0 auto;
    border: none;
cursor: pointer;  
}
  .gameover button:hover{
    background-color: slateblue;
    color: white;
    /* z-index: auto; */
  }
  @media(max-width:500px){
    .App{
      width: 50vh;
    }
  
    .start{
      margin: auto;
    }
    .gameover{
      width: 100%;
    }

  }